import React, { useState, useEffect } from "react";
import { Popover, Row, Col, Button, Divider, Checkbox } from "antd";
import axios from "axios";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import * as AppStorage from "../../utils/AppStorage";
import { hotline } from "../../assets/icons/Icon";
import "./FilterPopover.scss";

export const Content = (props) => {
  const [status, setStatus] = useState([]);
  const [urgencyLevel, setUrgencyLevel] = useState([]);
  const [hotiline, setHotline] = useState([]);
  const [hotlinesData, setHotlinesData] = useState([]);
  const [ selectedUegency, setSelectedUrgency ] = useState([]);
  const [ selectedHotline, setSelectedHotline ] = useState([]);
  const [ selectedStatus, setSelectedStatus ] = useState([]);
  const [ UrgencyLevelChecked, setUrgencyLevelChecked ] = useState([]);
  useEffect(() => {
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    axios
      .get(BASE_URL + "getUserallHotlines/" + user.user_data.user_id, options)
      .then((res) => {
        let data = [];
        res.data.map((value) =>
          data.push({
            label: value.hotline_name,
            value: value.hotline_id,
          })
        );
        setHotlinesData(data);
      });
  }, []);
useEffect(() => {
  console.log("props0",props.removeFilter,props.visible,selectedUegency)
  if(props.removeFilter.type!== undefined && !props.visible){
    onApplySubmit(false)
  }

  const checkLength= document.getElementsByClassName("ant-checkbox").length
 
}, [urgencyLevel, status, hotiline])
  useEffect(() => {
    if(props.removeFilter.type === 'urgencyLevel') {
      const removeUrgency =  selectedUegency.filter(it => it.value !== props.removeFilter.value);
      const rmUrg = urgencyLevel.filter(ur => ur !== props.removeFilter.value);
      console.log('... filter11', removeUrgency, rmUrg);
      for(let i=0;i<urgencyOptions.length;i++){
        console.log("in urgency loop",props.removeFilter.value , urgencyOptions[i].value)
        if(props.removeFilter.value===urgencyOptions[i].value){
          console.log("yesy")
          const val = urgencyOptions[i].label;
          console.log("val",val)
          for (let i = 0; i < document.getElementsByClassName(
            "ant-checkbox-wrapper").length; i++) {
          
            if(val === document.getElementsByClassName(
              "ant-checkbox-wrapper"
            )[i].childNodes[1].innerText){
              document.getElementsByClassName("ant-checkbox")[i].classList.remove("ant-checkbox-checked")
            }
          
          }
        }
      }
      setUrgencyLevel(rmUrg)
      setSelectedUrgency(removeUrgency);
    }
    if(props.removeFilter.type === 'urgencyHotline') {
      const removeHotline =  selectedHotline.filter(it => it.value !== props.removeFilter.value);
      const rmHot = hotiline.filter(ur => ur !== props.removeFilter.value);
      console.log("in htline loop",props.removeFilter.value)
      console.log("vaa0",props.removeFilter.type,hotlineOptions.length)
      for(let i=0;i<hotlineOptions.length;i++){
        
        if(props.removeFilter.value===hotlineOptions[i].value){
          console.log("yesy")
          const val = hotlineOptions[i].label;
          console.log("val",val)
          for (let i = 0; i < document.getElementsByClassName(
            "ant-checkbox-wrapper").length; i++) {
          
            if(val === document.getElementsByClassName(
              "ant-checkbox-wrapper"
            )[i].childNodes[1].innerText){
              document.getElementsByClassName("ant-checkbox")[i].classList.remove("ant-checkbox-checked")
            }
          
          }
        }
      }
      setHotline(rmHot)
      setSelectedHotline(removeHotline);
    
    }
    if(props.removeFilter.type === 'urgencyStatus') {
      const removeStatus =  selectedStatus.filter(it => it.value !== props.removeFilter.value);
      const rmStatus = status.filter(ur => ur !== props.removeFilter.value);
      setStatus(rmStatus)
      setSelectedStatus(removeStatus);

      console.log("vaa0",props.removeFilter.type,statusOptions.length)
      for(let i=0;i<statusOptions.length;i++){
        
        if(props.removeFilter.value===statusOptions[i].value){
          console.log("yesy")
          const val = statusOptions[i].label;
          console.log("val",val)
          for (let i = 0; i < document.getElementsByClassName(
            "ant-checkbox-wrapper").length; i++) {
          
            if(val === document.getElementsByClassName(
              "ant-checkbox-wrapper"
            )[i].childNodes[1].innerText){
              document.getElementsByClassName("ant-checkbox")[i].classList.remove("ant-checkbox-checked")
            }
          
          }
        }
      }
    }
    console.log("urgencyOptions",urgencyOptions.length, selectedUegency,selectedUegency.length)
    for(let i=0; i<=selectedUegency.length;i++){
      console.log("in seledddd",i)
    }
   

    // for (var i = 0; i <= 7; i++) {
    //   var antCheckboxWrapper = document.getElementsByClassName(
    //     "ant-checkbox-wrapper"
    //   )[i];
    //   if (antCheckboxWrapper) {
    //     antCheckboxWrapper.classList.remove("ant-checkbox-wrapper-checked");
    //   }
    //   var antCheckbox = document.getElementsByClassName("ant-checkbox")[i];
    //   if (antCheckbox) {
    //     antCheckbox.classList.remove("ant-checkbox-checked");
    //   }
    // }
  }, [props.removeFilter]);

  const urgencyOptions = [
    { label: "Low", value: 1 },
    { label: "Medium", value: 2 },
    { label: "High", value: 3 },
  ];
  const statusOptions = [
    { label: "New", value: 1 },
    { label: "Open", value: 2 },
    { label: "Closed", value: 3 },
  ];
  const hotlineOptions = hotlinesData;

  const onApplySubmit = (hide) => {
    let allCheckedOnes = {
      ...(status.length > 0 && { status_id: status }),
      ...(urgencyLevel.length > 0 && { urgency_level_id: urgencyLevel }),
      ...(hotiline.length > 0 && { hotline_id: hotiline }),
    };
console.log("in apply submit")
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const baseUrl =
      BASE_URL +
      "hotlineResponderConversation/?user_id=" +
      user.user_details.org_user_id;

    const finalUrl =
      props.searchString === ""
        ? baseUrl + "&filter=" + JSON.stringify(allCheckedOnes)
        : baseUrl +
          "&filter=" +
          JSON.stringify(allCheckedOnes) +
          "&search=" +
          props.searchString;

    axios.get(finalUrl, options).then((res) => {
      props.loadFilteredConversations(res.data, 'apply', selectedUegency, selectedStatus, selectedHotline );
    });
    if(hide){
      props.hide();
    }
    
  };

  function handleStatus(checkedValues) {
    let selected = selectedStatus;
    checkedValues.map( val => { 
      if(!selected.find(it => it.value === val)){
        // console.log("selectedStatus", selectedStatus, statusOptions.find(opt => opt.value === val) )
        selected.push(statusOptions.find(opt => opt.value === val))
      }
    })
    setSelectedStatus(selected);
    setStatus(checkedValues);
  }

  function handleHotline(checkedValues) {
    let selected = selectedHotline;
    checkedValues.map( val => { 
      if(!selected.find(it => it.value === val)){
        selected.push(hotlineOptions.find(opt => opt.value === val))
      }
    })
    setSelectedHotline(selected);
    setHotline(checkedValues);
  }

  function handleUrgencyLevel(checkedValues) {    
    let selected = selectedUegency;
    checkedValues.map( val => {
      if(!selected.find(it => it.value === val)){
        selected.push(urgencyOptions.find(opt => opt.value === val))
      }
    })
    setUrgencyLevelChecked(checkedValues)
    setSelectedUrgency(selected);

  }

  const handleReset = () => {
    for (var i = 0; i <= 7; i++) {
      var antCheckboxWrapper = document.getElementsByClassName(
        "ant-checkbox-wrapper"
      )[i];
      if (antCheckboxWrapper) {
        antCheckboxWrapper.classList.remove("ant-checkbox-wrapper-checked");
      }
      var antCheckbox = document.getElementsByClassName("ant-checkbox")[i];
      if (antCheckbox) {
        antCheckbox.classList.remove("ant-checkbox-checked");
      }
    }
    const token = authHeader();
    const user = JSON.parse(AppStorage.get("user"));
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };
    const baseUrl =
      BASE_URL +
      "hotlineResponderConversation/?user_id=" +
      user.user_details.org_user_id;

    axios.get(baseUrl, options).then((res) => {
      props.loadFilteredConversations(res.data, 'reset', selectedUegency, selectedStatus, selectedHotline);
      setUrgencyLevel([])
      setSelectedUrgency([]);
      setStatus([])
      setSelectedStatus([]);
      setSelectedHotline([]);
      setHotline([]);
    });
    props.hide();
   
  };

  return (
    <div>
      <Col span={24} style={{width: '325px'}}>
        <div className="overflow">
        <Row className="filter-wrapper align-items-center px-3 py-2">
          <Col span="12">
            <h6>Filter</h6>
          </Col>
          <Col span="12" className="d-flex justify-content-end">
            <Button
              type="link"
              className="link pl-0 pt-0"
              onClick={() => handleReset()}
            >
              Reset
            </Button>
          </Col>
        </Row>

        <Divider className="mt-0 mb-1 w-100" />
        <Row className="filter-wrapper align-items-center px-3 py-2">
          <div className="w-100 mb-4">
            <Col span="24" className="filter-type mb-3">
              <h6>Urgency Level</h6>
            </Col>
            <Col span="24">
              <Checkbox.Group
                className="d-flex flex-column flex-gap"
                name="urgencyLevel"
                options={urgencyOptions}
                onChange={handleUrgencyLevel}
              />
            </Col>
          </div>

          <div className="w-100 mb-4">
            <Col span="24" className="filter-type mb-3">
              <h6>Status</h6>
            </Col>
            <Col span="24">
              <Checkbox.Group
                className="d-flex flex-column flex-gap"
                name="status"
                options={statusOptions}
                onChange={handleStatus}
              />
            </Col>
          </div>
          <div className="w-100 mb-4">
            <Col span="24" className="filter-type mb-3">
              <h6>Hotline</h6>
            </Col>
            <Col span="24">
              <Checkbox.Group
                className="d-flex flex-column flex-gap"
                name="hotline"
                options={hotlineOptions}
                onChange={handleHotline}
              />
            </Col>
          </div>
          </Row>
          </div>
          <Row>
            <Col span="24" className="btn-row actions">
              <Button
                type="primary"
                className="mr-2"
                onClick={() => onApplySubmit(true)}
              >
                Apply
              </Button>
              <Button type="secondary" onClick={() => props.hide()}>
                Cancel
              </Button>
            </Col>
          </Row>
       
      </Col>
    </div>
  );
};

class FilterPopover extends React.Component {
  state = {
    visible: false,
  };

  hide = () => {
    this.setState({
      visible: false,
    });
  };

  handleVisibleChange = (visible) => {
    this.setState({ visible });
  };

  render() {
    return (
      <>
        <Popover
          placement="bottom"
          content={
            <Content
              hide={this.hide}
              searchString={this.props.searchString}
              removeFilter={this.props.removeFilter}
              loadFilteredConversations={this.props.loadFilteredConversations}
              visible={this.state.visible}
            />
          }
          trigger="click"
          visible={this.state.visible}
          onVisibleChange={this.handleVisibleChange}
        >
          <span className="icon icon-filter"></span>
        </Popover>
      </>
    );
  }
}

export default FilterPopover;
