import React, { Component } from "react";
import { Row, Col, Button, Divider, Tag } from "antd";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import moment from "moment"
import authHeader from "../../services/auth-header";

class Assigned extends Component {
  constructor() {
    super();
    this.state = {
      myclass: "",
      assigneEmp:[]
    };
    this.divclicked = this.divclicked.bind(this);
  }

  divclicked() {
    if (this.state.myclass === "") {
      this.setState({
        myclass: "rgt-panel",
      });
    } else {
      this.setState({
        myclass: "",
      });
    }
  }
  componentDidMount(){
    this.getAssigne(this.props.hotline_id);
  }

  getAssigne(hotlineId){
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      }
    };

    axios
      .get(BASE_URL + "getHotlineAssignee/"+hotlineId, options)
      .then((res) => {
        console.log("res", res)
        this.setState({assigneEmp:res.data})
      });
  }

  render() {
    const {assigneEmp} = this.state;
    return (
      <>
        <div id="rgt-panel" className={this.props.class}>
          <p>
            <Row>
              <Col span={24}>
                <Row className="filter-wrapper align-items-center justify-content-between px-3 pt-3">
                  <Col span="12">
                    <h6>Assigned To</h6>
                  </Col>
                  <Button
                    onClick={this.showModal}
                    type="link"
                    className="link pl-0 pt-0 align-items-center d-flex"
                  >
                    <span className="icon icon-close small" onClick={this.props.assigneClicked}/>
                  </Button>
                </Row>
                <Divider className="my-2 w-100" />
                <Col span="24" className="px-3 pt-2 assignees d-flex flex-column flex-gap">
                {
                 assigneEmp.map(item=>(
                 <Tag>{item["Assigned User_name"]}</Tag>
                 ))
                }
                </Col>
              </Col>
            </Row>
          </p>
        </div>
      </>
    );
  }
}

export default Assigned;
