import React, { Component } from "react";
import { Row, Col, Button, Divider, Timeline } from "antd";
import authHeader from "../../services/auth-header";
import { BASE_URL } from "../../userconfig";
import axios from "axios";
import moment from "moment";
import ReactHtmlParser from "react-html-parser";

const audit_log=[
  { id: 1, name: "Updated the Status to" },
  { id: 2, name: "Updated the Urgency Level to" },
  { id: 3, name: "Added new Note" },
  { id: 4, name: "View Hotline Converations" },
]

const obj_status = [
  { id: 1, name: "New" },
  { id: 2, name: "Open" },
  { id: 3, name: "Closed" },
];

const obj_urgencyLevel = [
  { id: 1, name: "Low" },
  { id: 2, name: "Medium" },
  { id: 3, name: "High" },
];

class Activity extends Component {
  constructor() {
    super();
    this.state = {
      myclass: "",
      activity:[]
    };
    this.divclicked = this.divclicked.bind(this);
  }

  divclicked() {
    if (this.state.myclass === "") {
      this.setState({
        myclass: "rgt-panel",
      });
    } else {
      this.setState({
        myclass: "",
      });
    }
  }

  componentDidMount(){
    this.getAcitivity(this.props.conversation_id);
  }

  getAcitivity(conversationId){
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      }
    };

    axios
      .get(BASE_URL + "getconversationActivity/?conversation_id="+conversationId, options)
      .then((res) => {
        console.log("res", res)
        this.setState({activity:res.data})
      });
  }


  mapAuditLog(auditlog){
    let str=""
    audit_log.map((k)=>{
      if(k.id === auditlog.audit_log_event_id){
        str = k.name;
        if(auditlog.status_value){
          obj_status.map((item)=>{
            if(auditlog.status_value==item.id){
              str=str+' <b>'+item.name+'</b>'
            }
          })
        }

        if(auditlog.urgency_level_value){
          obj_urgencyLevel.map((item)=>{
            if(auditlog.urgency_level_value==item.id){
              str=str+' <b>'+item.name+'</b>'
            }
          })
        }
      }  
    });
    return str;
  }


  render() {
    const {activity}=this.state;
    return (
      <>
        {/* <span onClick={this.divclicked} className="icon icon-create"></span> */}
        <div id="rgt-panel" className={this.props.class}>
          <p>
            <Row>
              <Col span={24}>
                <Row className="filter-wrapper align-items-center justify-content-between px-3 pt-3">
                  <Col span="12">
                    <h6>Activity</h6>
                  </Col>
                  <Button
                    onClick={this.showModal}
                    type="link"
                    className="link pl-0 pt-0 align-items-center d-flex"
                  >
                    <span className="icon icon-close small" onClick={this.props.activityClicked} />
                  </Button>
                </Row>
                <Divider className="my-2 w-100" />
                <Col span="24" className="px-3 pt-2">
                  <Timeline>
                   {activity.map((item)=>(
                      <Timeline.Item>
                      <span className="note-user">
                      {moment(item.created_on).format("MM/DD/yyyy  hh:mm A")} {item.name}
                      </span>
                      <br />
                      {ReactHtmlParser(this.mapAuditLog(item))}
                    </Timeline.Item>
                   ))}
                  </Timeline>
                  
                </Col>
              </Col>
            </Row>
          </p>
        </div>
      </>
    );
  }
}

export default Activity;